import styled from 'styled-components';
import React from 'react';
import fetch from 'isomorphic-unfetch';
const Div= styled.div`
overflow: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center; /*centers items on the line (the x-axis by default)*/
  align-items: center
`;
const Input = styled.input`
    width:100%;
`
const Sugest = styled.div`
    background:#fff;
    display: flex;
    
    height: 50vh;
    width:70vw;
    border: 2px solid #000;
    transition: 2s;
    padding: 0;
    margin: 0;
    list-style: none;
    
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    
    -webkit-flex-flow: row wrap;
    justify-content: space-around;
`;
const Img = styled.img`
    width :100%;
    height: 30vh;
`;
const Category = styled.div`
    border-right: 1px solid #9e9e9e;
    width:20%;
    word-wrap: break-word;
`;
const TileContent = styled.div`
    width:80%;
    display: flex;
    overflow: scroll;
    flex-wrap: wrap;
`;
const Card = styled.div`
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    flex-grow: 1;
    width: 33%;
`;
class IndexPage extends React.Component {
    state={value:''};
    static async getInitialProps () {
        const data = await import(`../products.json`)
        console.log(data);
        let category = [];
        let result = Object.keys(data).map(function(key) {
            // console.log(data[key].category);
            if(typeof category[data[key].category] === 'undefined') {
               category.push(data[key].category)
            }
            return data[key];
          }); 
        let unique = [...new Set(category)];
        unique = Object.keys(unique).map(function(key) {
            return unique[key];
        });
        return { result, unique }
    }
    handleChange = (event) => {
        console.log(event.target.value)
        this.setState({value: event.target.value});
    }
    render(){
        const {result, unique} = this.props; 
        const { value } = this.state;
        console.log(typeof unique);
        return (
            <Div>
                <span/>
                <div>
                    <br/>
                    <Input value={value} onChange={this.handleChange} />
                    <Sugest>
                        <Category>
                            Category Found:<br/>
                            {   unique.map((x:String)=>
                                x && x.includes(value) && <i>{x} </i>
                                )
                            }
                        </Category>
                        <TileContent>
                        {
                            result.map(x=>{
                                let {name, image_link, category} = x;
                                
                                return name && name.toLowerCase().includes(value) &&  <Card>
                                    <Img src={image_link}/>
                                    {name}
                                </Card>
                                }
                            )
                        }
                        </TileContent>
                    </Sugest>
                </div>
                <span/>
            </Div>
        )
    }
}

export default IndexPage;